___CSS_LOADER_EXPORT___.push([module.id, `#battleDialog {
  position: fixed;
  /* display: flex;
  justify-content: center;
  align-items: center; */
  z-index: 10;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  width: 100%;
  height: 100%;

  background-color: rgba(0, 0, 0, 0.829);
  border: 10px solid rgba(245, 179, 179, 0.034);
  /* border-radius: 50%; */
  animation: appear 0.2s ease-in 1;
}

#battleInterface {
  display: flex;
  /* display: none; */
  position: absolute;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 22px;
  /* width: 500px;
  height: 400px; */
  /* background-color: blue; */
  /* border-radius: 50%; */
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  /* border: 2px solid red;
  border-radius: 50%; */
}
#battleTitle {
  display: flex;
  flex-direction: column;
  align-items: center;

  height: 100px;

  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.199);
  padding: 5px 30px;

  /* box-shadow: #504f4fc4 0px 2px 4px, #b8b5b54d 0px 4px 5px 0.5px,
    #20202033 0px 1px 0px inset, #504f4fd0 3px 2px 4px inset; */

  box-shadow: inset #55555494 2px 1px 7px 1px, inset #242427b4 -2px -2px 6px 0px;

  border-style: double;

  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}
#battleTitle > .battleTitle {
  display: flex;
  color: white;
  font-size: 2rem;
}
#battleTitle > h4 {
  color: white;
  font-size: 1.3rem;
}

#avatars {
  width: 100%;
  height: 250px;
  /* background-color: yellow; */
  display: flex;
  justify-content: center;
  gap: 10px;
}

#avatars > div {
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  width: 200px;

  gap: 5px;

  border-radius: 5px;
  padding: 5px 30px;

  box-shadow: rgba(80, 79, 79, 0.4) 0px 2px 4px,
    rgba(27, 5, 5, 0.3) 0px 7px 13px -3px,
    rgba(24, 4, 4, 0.2) 0px -3px 0px inset;

  border-style: double;

  /* background-color: aqua; */
}
#avatars > div > div {
  display: flex;
  flex-direction: column;
  justify-content: end;
  align-items: center;
  /* width: 100%; */
  height: 100%;
  /* background-color: aqua; */
}
#avatars > div > div > div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  /* width: 100px; */
  font-size: 1rem;
  color: white;
  border-radius: 10px;

  padding: 1px 10px;
}
#avatars > div > div > div:nth-child(1) {
  background-color: rgba(0, 128, 0, 0.548);
}
#avatars > div > div > div:nth-child(2) {
  background-color: rgba(128, 0, 0, 0.459);
}

#avatars > div > div > div {
  display: flex;
  /* background-color: aqua; */
}
#avatars div h4 {
  position: absolute;
  font-size: 1.5rem;
  color: white;
  text-align: center;

  background-color: #000000;
  border-radius: 50%;

  transform: translateY(-23px);

  padding: 0px 20px;

  box-shadow: rgba(80, 79, 79, 0.767) 0px 2px 4px,
    rgba(94, 87, 87, 0.3) 0px 7px 13px -3px,
    rgba(99, 93, 93, 0.2) 0px -3px 0px inset,
    rgba(80, 79, 79, 0.815) -2px 2px 4px inset;
}
#defender {
  /* background-image: url(/ressources/images/avatars/leo1.webp); */
  background-size: cover;

  box-shadow: rgba(77, 16, 16, 0.4) 0px 2px 4px,
    rgba(75, 7, 7, 0.3) 0px 7px 13px -3px,
    rgba(224, 211, 211, 0.2) 0px -3px 0px inset;
}
#attacker {
  /* background-image: url(/ressources/images/avatars/sagittarius1.webp); */
  background-size: cover;

  box-shadow: rgba(77, 16, 16, 0.4) 0px 2px 4px,
    rgba(75, 7, 7, 0.3) 0px 7px 13px -3px,
    rgba(224, 211, 211, 0.2) 0px -3px 0px inset;

  animation: appear 0.5s ease 1;
}

#summonEmpower > button {
  font-size: 1.5rem;
  color: white;

  height: 50px;

  border-radius: 22px;
  padding: 5px 30px;

  box-shadow: rgba(80, 79, 79, 0.4) 0px 2px 4px,
    rgba(27, 5, 5, 0.3) 0px 7px 13px -3px,
    rgba(24, 4, 4, 0.2) 0px -3px 0px inset,
    rgba(80, 79, 79, 0.4) -2px 2px 4px inset;

  border-style: double;

  cursor: pointer;
}
#summonEmpower > button:hover {
  background-color: rgb(87, 86, 86);
}

#summonEmpower {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5px;
  border-radius: 20px;
}

#userPower {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;

  font-size: 1.5rem;
  color: white;
  /* background-color: rgb(0, 0, 0); */

  height: 50px;

  border-radius: 5px;
  padding: 5px 30px;

  visibility: hidden;
  /* box-shadow: rgba(80, 79, 79, 0.4) 0px 2px 4px,
    rgba(27, 5, 5, 0.3) 0px 7px 13px -3px,
    rgba(24, 4, 4, 0.2) 0px -3px 0px inset,
    rgba(80, 79, 79, 0.4) -2px 2px 4px inset; */
  /* border-radius: 20px; */
}

#soulsValue {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#soulsNumber {
  font-size: 1.3rem;
  /* background-color: red; */
}
#up {
  color: white;
  font-size: 0.8rem;
  cursor: pointer;
}
#up:hover {
  color: green;
}
#down {
  color: white;
  font-size: 0.8rem;
  cursor: pointer;
}
#down:hover {
  color: red;
}

#hpAndDmg {
  display: flex;
  /* flex-direction: column; */
  gap: 10px;
}
#hp {
  display: flex;
  gap: 3px;
}
#dmg {
  display: flex;
  gap: 3px;
}

/* #defenderImage {
  height: 100%;
  width: 100%;
  border: 2px solid red;
} */
